@font-face {
font-family: "A.C.M.E. Secret Agent";
src: local("A.C.M.E. Secret Agent"),
 url("../assets/fonts/acmesa.TTF") format("truetype");
font-weight: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
  transition: all 0.5s ease;
}

body {
  margin: 0 auto;
  max-width: 1500px;
  background-color: #08091d;
  font-family: Roboto Serif;
  background-image: radial-gradient(ellipse farthest-side,#1a1f44,#08091d);
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.ymaps-2-1-79-gotoymaps__text{
  color: black;
}

.ymaps-2-1-79-copyright__text{
  color: red;
}
.react-multi-carousel-track {
  padding-bottom: 40px !important;
}

.tc-background-yellow {
  min-width: 160px !important;
  border-radius: 0 !important;
  background: #F9A037 !important;
  color: black !important;
  text-shadow: none !important;
  text-transform: capitalize !important;
  padding: 5px 80px !important;
  margin: 0 auto !important;
  width: 100% !important;
}

@media(min-width: 600px) {
  .react-multi-carousel-track {
    padding-bottom: 50px !important;
  }
  .tc-background-yellow {
    margin: 0 !important;
    border-radius: 10px !important;
    padding: 0 50px !important;
    max-width: 250px !important;
  }
}

.swiper-wrapper {
  padding-bottom: 30px;
}
.swiper-pagination-bullet {
  background-color: gray !important;
}
.swiper-pagination {
  position: relative;
  bottom: 10px !important;
}